      box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
      padding: 40px;
      border-radius: 20px;
  }
  
  .login-logo{
      height: 60px;
      object-fit: contain;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      margin: auto;
      margin-bottom: 20px !important;
  }
  
  .input-label{
      font-size: 20px;
      font-weight: 800;
      margin: 10px 0px !important;
      color: black;
      font-family: verizonBoldFont;
  }
  
