      border-bottom: 2px solid black !important;
      padding: 0px !important;
      font-size: 20px;
      padding-left: 10px !important;
      font-family: verizonFont;
  }
  
  .input-fields:focus{
      outline: none !important;
      border-bottom: 2px solid #d70819 !important;
  }
  
  .input-fields:hover{
      border-bottom: 2px solid #d70819 !important;
  }
  
  .login-button{
      width: 150px;
      height: 40px;
      background-color: black;
      border-radius: 20px;
      font-size: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
      cursor: pointer;
      position: relative;
      top:auto;
      left: auto;
      margin: auto;
      margin-top: 20px;
      font-family: verizonFont;
      border: none;
  }
  
  .login-button:hover{
